const mixins = {
  data() {
    return {
      dialogArryrow: null,//多批次弹出框row
      validRules: {
        goods_name: [{ required: true, message: "商品名称必填" }],
        batch: [{ required: true, message: "批次不能为空" }],
        real_num: [{ required: true, message: "数量必填" }],
        sale_price: [{ required: true, message: "单价必填" }],
        total_price: [{ required: true, message: "合计不能为空" }]
      },
      tableColumn: [{
        type: 'checkbox',
        width: 40
      },
      {
        type: 'index',
        title: '序号',
        width: 50
      },
      {
        field: 'goods_id',
        title: '商品编号',
        width: 80,
        showOverflow: "tooltip",
        showHeaderOverflow: "tooltip",
      },
      {
        field: 'goods_name',
        title: '商品名称',
        width: 300,
        showOverflow: "tooltip",
        showHeaderOverflow: "tooltip",
        editRender: {
          name: 'vGoods',
          getParams: (scope) => {
            let _this = this
            return {
              params: {
                company_id: this.params.company_id || 0,
                customer_id: this.params.customer_id || 0,
                warehouse_id: this.params.warehouse_id || 0,
                bill_type: 2,
                isTotal: 1
              },
              getIcon() { // 渲染器内部图标 返回一个数组 (非必传 默认为空)
                return [{
                  icon: 'el-icon-plus', // 渲染器图标 基于element图标 (非必传 默认为查询图标)
                  iconMethod() { // 渲染器图标点击方法 (非必传)
                    _this.goSelectShop(scope)
                  }
                }]
              },
            }
          },
          events: {
            change: this.changeGoods
          }
        }
      },
      {
        field: 'batch',
        title: '年周/批次',
        showOverflow: "tooltip",
        showHeaderOverflow: "tooltip",
        width: 200,
        editRender: {
          autoselect: true,
          name: 'vBatch',
          getParams: (scope) => {
            let _this = this
            return {
              params: {
                company_id: this.params.company_id,
                warehouse_id: this.params.warehouse_id,
                goods_id: scope.row.goods_id,
                bill_type: 2
              }
            }
          },
          events: {
            change: (val, { row }) => {
              if (!row.goods_id) {
                return false
              }
              const {
                production_time,
                batch_id,
                batch,
                stocks,
                available_stocks,
                warehouse_id,
                unit_name,
                warehouse_name,
                allocation_id,
                allocation_name
              } = val;
              Object.assign(row, {
                production_time,
                batch_id,
                batch,
                stocks,
                available_stocks,
                warehouse_id,
                unit_name,
                warehouse_name,
                allocation_id,
                allocation_name
              });
            }
          }
        }
      },
      {
        field: 'warehouse_name',
        title: '仓库名称',
        showOverflow: "tooltip",
        showHeaderOverflow: "tooltip",
        width: 120,
      }, {
        field: 'allocation_name',
        title: '货位名称',
        showOverflow: "tooltip",
        showHeaderOverflow: "tooltip",
        width: 120,
      }, {
        field: 'unit_name',
        title: '单位',
        showOverflow: "tooltip",
        showHeaderOverflow: "tooltip",
        width: 110
      },
      {
        field: 'real_num',
        title: '数量',
        showOverflow: "tooltip",
        showHeaderOverflow: "tooltip",
        headerAlign: "right", // 表头对齐方式
        align: "right", // 列对齐方式
        editRender: {
          autoselect: true,
          name: 'number',
          events: {
            input: (scope) => {
              this.handleEdit(scope.row, 'real_num', scope.$rowIndex)
            }
          }
        }
      },
      {
        field: 'sale_price',
        title: '单价',
        showOverflow: "tooltip",
        showHeaderOverflow: "tooltip",
        headerAlign: "right", // 表头对齐方式
        align: "right", // 列对齐方式
        editRender: {
          autoselect: true,
          name: 'vNumber',
          events: {
            input: (scope) => {
              this.handleEdit(scope.row, 'sale_price', scope.$rowIndex)
            }
          },
        }
      },
      {
        field: 'total_price',
        title: '小计金额',
        showOverflow: "tooltip",
        showHeaderOverflow: "tooltip",
        headerAlign: "right", // 表头对齐方式
        align: "right", // 列对齐方式
        editRender: {
          autoselect: true,
          name: 'vNumber',
          events: {
            input: (scope) => {
              this.handleEdit(scope.row, 'total_price', scope.$rowIndex)
            }
          },
        }
      }, {
        field: 'remark',
        title: '备注',
        width: 150,
        showOverflow: "tooltip",
        showHeaderOverflow: "tooltip",
        editRender: {
          name: 'Vinput',
          formatter: 'String'
        }
      }
      ],
      tableData: [],
    }
  },
  methods: {
    activeMethod({ row, rowIndex, column, columnIndex }) {
      if (column.property == 'batch' || column.property == 'real_num' || column.property == 'sale_price' || column.property == 'remark' || column.property == 'total_price') {
        if (row.goods_id) {
          return true
        } else {
          return false
        }
      } else {
        return true
      }

    },
    rowClassName({ $rowIndex, column, columnIndex, $columnIndex }) {
      if (this.styleArry.includes($rowIndex + 1 + '')) {
        return {
          backgroundColor: "#fdecea",
          color: "#5F5F5F"
        };
      }
    },
    async fullValidEvent() {
      let selectRecords = this.$refs.xTable
        .getTableData()
        .tableData.slice(0, -1);
      const errMap = await this.$refs.xTable
        .validate(selectRecords)
        .catch(errMap => errMap);
      if (errMap) {
        let name = null;
        for (var key in errMap) {
          name = key;
        }
        this.$XModal.message({
          status: "error",
          message: () => {
            return [
              <div class="red" style="max-height: 400px;overflow: auto;">
                <div>{errMap[name].rule.message}</div>
              </div>
            ];
          }
        });
        return false
      } else {
        return true
      }
    },
    addRow() {
      if (!this.params.company_id) {
        this.$XModal.message({
          status: "error",
          message: () => {
            return [
              <div class="red" style="max-height: 400px;overflow: auto;">
                <div>请选择组织</div>
              </div>
            ];
          }
        });
        return false
      }
      let xtable = this.$refs.xTable;
      xtable
        .createRow({
          warehouse_id: ""
        })
        .then(newRow => {
          xtable.insertAt(newRow, -1);
        });
    },
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (column.property == 'total_price') {
            this.settlement_price = this.$utils.sum(data, column.property).toFixed(2)
            return this.$utils.sum(data, column.property).toFixed(2);
          } else if (column.property == 'real_num') {
            return this.$utils.sum(data, column.property);
          }
        })
      ];
    }
  }
}
export default mixins