<template>
  <div v-loading="loading_demo" element-loading-text="列表正在生成中" element-loading-spinner="el-icon-loading" element-loading-background="rgba(255, 255, 255, 0.8)">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">其他出库</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{title}}其他出库</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" ref="TableDataRef" :model="params" class="demo-form-inline">
        <el-form-item label="库存组织" size="mini" prop="company_id" :rules="[{ required: true, message: '请选择库存组织', trigger: 'change' }]">
          {{(function(){if(params.company_id == 0)params.company_id = null})()}}
          <el-select v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="单据号" size="mini">
          <span v-if="title=='修改'">{{params.outstock_sn}}</span>
          <el-input v-else v-model="params.outstock_sn" disabled></el-input>
        </el-form-item>
        <el-form-item label="关联单据号" size="mini">
          <span v-if="title=='修改'">{{params.order_sn}}</span>
          <el-input v-else v-model="params.order_sn" disabled></el-input>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="[{ required: true, message: '请选择业务日期', trigger: 'change' }]">
          <el-date-picker :clearable="false" v-model="params.business_time" type="date" placeholder="选择日期" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="出库类型" size="mini" prop="outstock_type_id" :rules="[{ required: true, message: '请选择出库类型', trigger: 'change' }]">
          <el-select v-model="params.outstock_type_id" placeholder="请选择" disabled>
            <el-option label="其他出库" :value="10003"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" size="mini">
          {{(function(){if(params.customer_id == 0)params.customer_id = null})()}}
          <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @change="getCustomerInfo" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @focus="DepartmentListInvok" @change="departmentChange">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务员" size="mini">
          {{(function(){if(params.business_id == 0)params.business_id = null})()}}
          <el-select clearable v-model="params.business_id" @focus="EmployeeListInvok">
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="收货人" size="mini" prop="consignee_name">
          <el-input v-model="params.consignee_name"></el-input>
        </el-form-item>
        <el-form-item label="收货人电话" size="mini">
          <el-input v-model="params.consignee_mobile"></el-input>
        </el-form-item>
        <el-form-item label="所属区域" size="mini">
          <getAreaList v-if="defaultArea && defaultArea.length" @changeDefaultArea="changeDefaultArea" :defaultArea="defaultArea" />
        </el-form-item>
        <el-form-item size="mini" label="详细地址">
          <el-input v-model="params.address"></el-input>
        </el-form-item>
        <el-form-item label="仓库名称" size="mini">
          <el-select clearable v-model="params.warehouse_id" filterable placeholder="请选择" @focus="warehouseInvok" @change="warehouse">
            <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList"></el-option>
          </el-select>
        </el-form-item>
        <!-- 1265需求 -->
        <el-form-item label="回款方式" size="mini" prop="payment_method" :rules="rules.payment_method">
          <el-select v-model="params.payment_method" @change="cleasrsettlement_customer_id">
            <el-option label="客户自结" :value="1">客户自结</el-option>
            <el-option label="物流代收" :value="2">物流代收</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="物流公司" size="mini">
          <el-select clearable v-model="params.settlement_customer_id" filterable @change="logisticsChanges" @focus="logisticsInvok" :filter-method="logisticsInvok">
            <el-option :label="item.logistics_name" :value="item.logistics_id" :key="item.logistics_id" v-for="item in logisticsList"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="送货需求" size="mini">
          {{(function(){if(params.delivery_demand_id == 0)params.delivery_demand_id = null})()}}
          <el-select clearable v-model="params.delivery_demand_id" @focus="getDeliveryDemandListInvok">
            <el-option v-for="item in deliveryDemandList" :label="item.delivery_demand_name" :value="item.delivery_demand_id" :key="item.delivery_demand_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="交货方式" size="mini">
          {{(function(){if(params.hand_over_id == 0)params.hand_over_id = null})()}}
          <el-select clearable v-model="params.hand_over_id" @focus="getHandoverListInvok">
            <el-option v-for="item in handoverList" :label="item.hand_over_name" :value="item.hand_over_id" :key="item.hand_over_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="打印次数" size="mini">
          <span v-if="title=='修改'">{{params.print_times}}</span>
          <el-input v-else v-model="params.print_times" disabled></el-input>
        </el-form-item>
        <el-form-item label="单据状态" size="mini">
          <span v-if="title=='修改'">{{params.auditor_status_name}}</span>
          <el-input v-else v-model="params.auditor_status_name" disabled></el-input>
        </el-form-item>
        <el-form-item label="订单号" size="mini">
          <el-input v-model="params.sales_order_sn" disabled maxlength="100"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addRow" class="addRow" size="mini">增行</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <vxe-grid border ref="xTable" :row-style="rowClassName" show-overflow='tooltip' show-footer="true" :edit-rules="validRules" size="mini" :footer-method="footerMethod" max-height="500px" :data="tableData" resizable height="500px" column-min-width="100" :mouse-config="{selected: true}" :columns="tableColumn" :keyboard-config="{isArrow: true, isDel: false, isTab: true, isEdit: true}" :edit-config="{trigger: 'click', mode: 'cell',autoClear:false,activeMethod:activeMethod}"></vxe-grid>
    </div>
    <div class="bzstyle">
      <span>备注</span>
      <el-input type="text" maxlength="300" v-model="params.abstract" placeholder="请输入"></el-input>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack()">取 消</el-button>
      <el-button type="primary" @click="click_submit()">确 定</el-button>
    </div>
    <!-- 选择商品 -->
    <goodsPopout v-if='dialogChooseProducts' @close='dialogChooseProducts = false' :searchParams='{company_id:params.company_id,
          warehouse_id:params.warehouse_id,
          warehouse_name:params.warehouse_name,
          customer_id: params.customer_id
          }' @submit="chooseProducts_confirm" />
  </div>
</template>
<script>
import goodsAddress from "@/components/element/inventory/share/goodsAddressOutStorage"; //货位弹框
import getAreaList from "common/components/public/getAreaList";
import { mapActions, mapGetters } from "vuex";
import mixin from "./salesoutstorage.js";
import {
  getData,
  opthionList,
  repertory
} from "common/utils/public_function/commonUtils";
import { tableRowChecked } from "@/utils/commonMethod";
export default {
  mixins: [mixin],
  name: "instorageOthenoutstorageSalesoutstorageAdd",
  data() {
    return {
      authority: {},
      title: "新增",
      indexchart: {},
      tableData: [],
      styleArry: [],
      dialogChooseProducts: false, // 选择商品
      addChandise: false,
      rowIndex: null, //选择的当前表格索引
      state: false, //改变cheackbox状态
      multipleSelection: [],
      params: {
        payment_method: 1
      },
      showAllocationList: true,
      companyList: [],
      showDepartment: true,
      defaultArea: [0], //默认的区域id
      allocationInventoryList: [], //货位列表
      loading_demo: false,
      loading: false,
      cityList: [],
      settlement_price: 0, //可蒙需要的总计(单据金额)
      saveNumBatchObj: {} // 保存修改页面的goods_info中的可用库存和batch_id
    };
  },
  components: {
    goodsAddress, //货位列表
    getAreaList //区域列表
  },
  computed: {
    ...mapGetters(["salesoutstorageAddGet"])
  },
  methods: {
    ...mapActions(["othenoutstorageIndexACT"]),
    // 验证条件下 设置table td样式
    //  打开选择商品得弹框
    goSelectShop(scope) {
      this.indexchart = scope.row;
      this.dialogChooseProducts = true;
    },
    // 商品选择完毕 并关闭弹框
    chooseProducts_confirm(getArr) {
      getArr = getArr.map(val => {
        val.sale_price = val.unit_price;
        val.real_num = val.goods_num;
        this.handleEdit(val, "real_num");
        return val;
      });
      this.dialogChooseProducts = false;
      let xtable = this.$refs.xTable;
      xtable.insertAt(getArr, this.indexchart);
    },
    // wz 1265需求 start
    changeGoods(value, scope) {
      const row = scope.row;
      Object.assign(row, {
        batch_id: null, // 年周/批次编号
        goods_id: value.goods_id, // 商品编号
        goods_name: value.goods_name, // 商品名称
        available_stocks: "", // 可用库存
        batch: "", // 年周/批次
        unit_name: value.unit_name, // 单位
        warehouse_name: "", // 仓库名称
        allocation_name: "", // 货位名称
        allocation_id: "", // 货位ID
        sale_price: value.unit_price, // 单价
        discount_price: 0,
        remark: "" // 备注
      });
      var hasEmptyData = true;
      let xtable = this.$refs.xTable;
      xtable.getTableData().fullData.forEach(item => {
        if (!item.goods_id) hasEmptyData = false;
      });
      if (hasEmptyData) {
        this.addRow();
      }
    },
    // 添加 batchList 列表字段
    batchListInitalList(obj, rowObj = {}, igFiledArr = []) {
      let setFiled = [
        "batch",
        "batch_id",
        "warehouse_id",
        "warehouse_name",
        "allocation_id",
        "allocation_name",
        "production_time",
        "sale_price",
        "available_stocks"
      ]; // 自动设置选中项
      setFiled.forEach(key => {
        if (key == "available_stocks") {
          // 进入修改页面验证需要使用
          let getSaveBatchId = this.saveNumBatchObj[obj["batch_id"]];
          let getAvailable_stocks = obj[key];
          if (getSaveBatchId) {
            getAvailable_stocks = getSaveBatchId;
          }
          rowObj[key] = getAvailable_stocks;
        } else {
          if (igFiledArr.indexOf(key) < 0) {
            rowObj[key] = obj[key];
          }
        }
      });
      return rowObj;
    },
    // wz 1265需求 end
    cleasrsettlement_customer_id() {
      this.$set(this.params, "settlement_customer_id", "");
    },
    goBack() {
      this.othenoutstorageIndexACT({
        goPage: this.salesoutstorageAddGet.goPage,
        params: this.salesoutstorageAddGet.params,
        pageSize: this.salesoutstorageAddGet.pageSize
      });
      this.$router.push({
        path: "/inventory/othenoutstorage"
      });
    },
    // 保留两位小数
    toFixedTwo(num) {
      const _isNumber = Number(num);
      const _isNaN = isNaN(_isNumber);
      if (!_isNaN) {
        num = parseFloat(num).toFixed(2);
      }
      return num;
    },
    // input change 事件
    handleEdit(row, name, index) {
      let _this = this;
      var obj = {
        num: "real_num", // 商品数量的key
        price: "sale_price", // 商品价格的key
        goodsPrice: "total_price" // 商品总金额的key  数量 * 单价
      };
      this.$publicFution.computeTotal(row, obj, name, this);
      this.$refs.xTable.updateFooter();
      // 合并计算
    },
    opens() {
      this.state = true;
    },
    goodsdialog(val) {
      this.addChandise = true;
    },
    attribute(val) {
      this.addChandise = val;
    },
    getCustomerInfo(val, status, dialogdata) {
      if (val) {
        var company_id = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].company_id;
      }
      let params = {
        customer_id: val,
        company_id: company_id
      };
      if (!val) return false;
      // 请求接口 获取用户的信息详情
      this.$axMethod
        .getCustomerInfoNEWqx(params)
        .then(({ data }) => {
          if (data.code == 200) {
            let arr = [
              "business_id",
              "business_name",
              "hand_over_id",
              "hand_over_name",
              "delivery_demand_id",
              "delivery_demand_name"
            ];
            arr.forEach(key => {
              this.$set(this.params, key, data.data[key]);
            });
            this.employeeList = [
              {
                employee_id: data.data.business_id,
                employee_name: data.data.business_name
              }
            ];
          }
        })
        .then(res => {
          this.$axMethod.getDefaultAddress(params).then(({ data }) => {
            if (data.code == 200) {
              this.$set(this.params, "address", data.data.address);
              this.$set(
                this.params,
                "consignee_name",
                data.data.consignee_name
              );
              this.$set(
                this.params,
                "consignee_mobile",
                data.data.consignee_mobile
              );
              this.defaultAreaName = `${data.data.province_name}/${data.data.city_name}/${data.data.area_name}`;
              this.defaultArea = [
                data.data.province_id,
                data.data.city_id,
                data.data.area_id
              ];
              this.params.cityList = this.defaultArea.join(",");
              this.params.province_id = this.defaultArea[0];
              this.params.city_id = this.defaultArea[1];
              this.params.area_id = this.defaultArea[2];
            }
          });
        });
    },
    deletes() {
      let xtable = this.$refs.xTable;
      xtable.removeSelecteds();
      xtable.updateFooter();
      this.styleArry = [];
    },
    // 点击确定新增
    click_submit() {
      this.$refs.xTable.clearActived();
      let xTable = this.$refs.xTable.getTableData().fullData.filter(val => {
        return val.goods_id;
      });
      if (!xTable.length) {
        this.$message.warning("请完善数据表格");
        return false;
      }
      if (
        this.params.payment_method == 2 &&
        !this.params.settlement_customer_id
      ) {
        this.$message.warning("请选择物流公司");
        return false;
      }
      this.params.settlement_price = this.settlement_price;
      this.fullValidEvent().then(res => {
        if (res) {
          try {
            var goods_info = [];
            xTable.forEach((val, index) => {
              //判断输入是否大于批次数量
              if (val.real_num > val.available_stocks) {
                this.$XModal.message({
                  status: "error",
                  message: () => {
                    this.$refs.xTable.setActiveCell(val, "real_num");
                    return [
                      <div
                        class="red"
                        style="max-height: 400px;overflow: auto;"
                      >
                        <div>第{index + 1}行数量超过商品批次可用数量</div>
                      </div>
                    ];
                  }
                });
                throw Error(false);
              } else {
                goods_info.push(this.portParamsBuild(val, index));
              }
            });
          } catch (e) {
            return false;
          }
          // 校验安全库存金额参数
          var box = goods_info.map(item => {
            return {
              warehouse_id: item.warehouse_id,
              warehouse_name: item.warehouse_name,
              allocation_id: item.allocation_id,
              goods_id: item.goods_id,
              batch: item.batch,
              order_goods_id:item.order_goods_id||0,
              sale_price:item.sale_price||0,
              batch_id: item.batch_id,
              goods_num: item.real_num
            };
          });
          // 校验对接车郎中
          this.$baseData
            .checkCarDoctor({
              company_id: this.params.company_id,
              company_name: this.params.company_name,
              bill_type: 10,
              bill_id: this.params.outstock_id,
              operation_type: this.params.outstock_id ? 2 : 1,
              goods_info: JSON.stringify(box)
            })
            .then(() => {
              // 校验安全库存金额
              this.$baseData
                .checkComputation(goods_info, {
                  goods_num: "real_num",
                  total_price: "total_price",
                  goods_price: "unit_price"
                })
                .then(() => {
                  this.$axMethod
                    .safetyStockCheck({
                      bill_sn: this.params.outstock_sn,
                      bill_id: this.params.outstock_id,
                      company_id: this.params.company_id,
                      company_name: this.params.company_name,
                      business_time: this.params.business_time,
                      bill_logogram: "qtck",
                      order_type: 1,
                      goods_info: JSON.stringify(box)
                    })
                    .then(data => {
                      let submitFrom = Object.assign({}, this.params, {
                        goods_info: JSON.stringify(goods_info)
                      });
                      let api = !this.params.outstock_id
                        ? "otherStockAddOutstock"
                        : "otherStockEditOutstock";
                      let vm = this;
                      repertory(
                        this,
                        {
                          company_id: this.params.company_id,
                          goods_info: JSON.stringify(goods_info),
                          status: 1,
                          type: 4
                        },
                        function() {
                          vm.$axMethod[api](submitFrom).then(({ data }) => {
                            if (data.code == "200") {
                              vm.$message({
                                message: data.data,
                                type: "success"
                              });
                              vm.goBack();
                            } else {
                              vm.styleArry = data.data;
                            }
                          });
                        }
                      );
                    });
                });
            });
        }
      });
      return false;
    },
    // 添加/修改 goods_info 构建
    portParamsBuild(item, index) {
      if (this.title == "新增") {
        return {
          serial_number: index + 1,
          goods_id: item.goods_id,
          batch: item.batch,
          batch_id: item.batch_id,
          warehouse_id: item.warehouse_id,
          warehouse_name: item.warehouse_name,
          allocation_id: item.allocation_id,
          allocation_name: item.allocation_name,
          settlement_price: item.settlement_price,
          unit_price: item.sale_price,
          total_price: item.total_price,
          production_time: item.production_time,
          discount_price: item.discount_price ? item.discount_price : 0,
          outstock_goods_id: item.outstock_goods_id
            ? item.outstock_goods_id
            : 0,
          discount_unit_price: item.discount_unit_price
            ? item.discount_unit_price
            : 0,
          unit_credit_price: item.unit_credit_price,
          unit_rebate_price: item.unit_rebate_price,
          unit_deposits_price: item.unit_deposits_price,
          business_type_id: 10001, // 后台规定默认 10001
          business_type_name: "商品订购",
          company_id: this.params.company_id,
          company_name: this.params.company_name,
          remark: item.remark ? item.remark : "",
          real_num: item.real_num
        };
      } else {
        return {
          serial_number: index + 1,
          goods_id: item.goods_id,
          settlement_price: item.settlement_price,
          goods_name:
            item.goods_name instanceof Object
              ? item.goods_name.goods_name
              : item.goods_name,
          // goods_sn: item.goods_sn,
          warehouse_id: item.warehouse_id,
          warehouse_name: item.warehouse_name,
          outstock_goods_id: item.outstock_goods_id
            ? item.outstock_goods_id
            : 0,
          batch_id: item.batch_id,
          batch: item.batch,
          production_time: item.production_time,
          // goods_num: item.goods_num,
          unit_price: item.sale_price,
          total_price: item.total_price,
          // cost_price: item.cost_price || "",
          remark: !!item.remark ? item.remark : "",
          // allocation_id_str: "",
          // business_type_id: 10001, // 后台规定默认 10001 // 等后台重构完 2019/11/20 16:30
          allocation_id: item.allocation_id,
          real_num: item.real_num,
          goods_num: item.real_num
        };
      }
    },
    companyChange(val) {
      // wz alter
      if (val) {
        let arr = this.companyList.filter(obj => obj.company_id == val);
        if (arr.length > 0) {
          this.params.company_name = arr[0].company_name;
        }
      }
      this.$set(this.params, "business_id", "");
      if (val != 1) {
        this.$set(this.params, "customer_id", null);
        this.$set(this.params, "warehouse_id", null);
        this.$set(this.params, "goods_info", []);
        this.$set(this.params, "department_id", "");
        this.$set(this.params, "department_name", "");
      }
      this.$refs.xTable.clearData();
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    warehouseChange(row) {
      row.allocation_id_str = [];
    },
    warehouse(val) {
      if (val) {
        this.params.warehouse_name = this.warehouseList.filter(
          obj => obj.warehouse_id === val
        )[0].warehouse_name;
      }
    },
    logisticsChange(val) {
      //物流搜索
      this.$common.options("getLogisticsListOption", this.logisticsList, {
        company_id: this.params.company_id,
        logistics_name: val
      });
    },
    logisticsChanges(val) {
      if (val) {
        this.params.logistics_name = this.logisticsList.filter(
          obj => obj.logistics_id == val
        )[0].logistics_name;
      }
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    }
  },
  created() {
    this.params = Object.assign(this.params, {
      payment_method: 1
    });
  },
  mounted() {
    this.title = this.salesoutstorageAddGet.outstock_id ? "修改" : "新增";
    if (this.salesoutstorageAddGet.outstock_id) {
      this.$axMethod
        .otherStockCheckOutstock({
          outstock_id: this.salesoutstorageAddGet.outstock_id
        })
        .then(({ data }) => {
          if (data.code == 200) {
            if (data.data.settlement_customer_id == 0) {
              data.data.settlement_customer_id = "";
            }
            this.defaultArea = [
              data.data.province_id,
              data.data.city_id,
              data.data.area_id
            ];
            // 清除id为0 的字段
            if (data.data.warehouse_id == 0) data.data.warehouse_id = "";
            this.params.cityList = this.defaultArea.join(",");
            this.params.province_id = this.defaultArea[0];
            this.params.city_id = this.defaultArea[1];
            this.params.area_id = this.defaultArea[2];
            this.$set(this, "tableData", data.data.goods_info);
            delete data.data.goods_info;
            this.params = data.data;
            Object.assign(data.data, {
              employee_id: data.data.business_id,
              employee_name: data.data.business_name
            });
            if (data.data.settlement_customer_id) {
              this.logisticsList.push({
                logistics_id: data.data.settlement_customer_id,
                logistics_name: data.data.settlement_customer_name
              });
            }
            opthionList(this, data.data);
          }
        });
    } else {
      let { employee_id, company_admin_id } = localStorage;
      if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
        this.$set(this, "params", {
          outstock_type_id: 10003,
          business_time: this.$publicFution.formatData(new Date()),
          company_id: Number(localStorage.company_id),
          payment_method: 1,
          cityList: ""
        });
      } else {
        this.$set(this, "params", {
          outstock_type_id: 10003,
          business_time: this.$publicFution.formatData(new Date()),
          company_id: Number(localStorage.company_id),
          department_id: Number(localStorage.department_id),
          business_id: Number(localStorage.employee_id),
          payment_method: 1,
          cityList: ""
        });
      }
      opthionList(this, localStorage);
      this.addRow();
    }
  }
};
</script>

