 * @Last Modified time: 2018-08-30 15:19:52
 * 出库货位列表
 *
 * selectData：参数
 * 返回："getAllocationArr" 所选列表数据
 */
<template>

  <div>
    <!--货位列表-->
    <el-dialog :close-on-click-modal="false" title="货位列表" class="dialogForm" :visible="true" width="800px" @close="closeDialog">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="货位名称" size="mini">
            <el-input v-model="params.allocation_name" class="shortSearchInput"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <span class="pr20">{{TableData.goods_info.warehouse_name }}</span>
      <span class="pr20">{{TableData.goods_info.goods_name}}</span>
      <br />
      <br />
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @row-click="handleSelectionChange" v-loading="loading1" max-height="600">
          <el-table-column :show-overflow-tooltip="true" width="50" label="选择">
            <template slot-scope="scope">
              <el-radio v-model="radio" :label="scope.row.allocation_id">{{null}}</el-radio>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位名称"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="stocks" label="货位目前货品数量"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" label="订单商品数量">
            <template slot-scope="scope">
              <el-input placeholder="请输入" v-model="scope.row.num" suffix-icon="el-icon-edit-outline" @change="toPositiveNum(scope.row)"></el-input>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @current-change="handleCurrentChange" :current-page.sync="goPage" :page-sizes="[30, 50, 100,500]" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog()">取 消</el-button>
        <el-button type="primary" @click="submit()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getData } from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      TableData: {
        tableData: [],
        goods_info: {},
        total: 10
      },
      goPage: 1, //默认搜索页面
      loading: true,
      manageList: [], //仓库
      multipleSelection: [],
      params: {},
      radio: null
    };
  },
  props: {
    //请求货位列表参数
    selectData: {
      type: [Object],
      default: "",
      required: true
    }
  },
  methods: {
    toPositiveNum(row) {
      this.$nextTick(() => {
        if (row.num < 0 || isNaN(row.num)) {
          row.num = 0;
        }
        if (row.num > row.stocks) {
          this.$message.warning("订单商品数量不能大于可用数量");
          row.num = 0;
        }
        row.num = Math.ceil(row.num);
      });
    },
    closeDialog() {
      this.$parent.goodsAddressVisible = false;
    },
    initData() {
      this.$axMethod.publicsAllocationList(this.params).then(res => {
        if (res.data.code == 200) {
          this.loading = false;
          this.goods_info = res.data.data.goods_info;
          this.TableData.tableData = res.data.data.allocation_list.data;
          this.TableData.total = res.data.data.allocation_list.total;
        }
      });
    },
    handleSelectionChange(val) {
      this.radio = val.allocation_id;
      this.multipleSelection = val;
    },
    handleCurrentChange(val) {
      this.params.page = val;
      this.initData();
    },
    onSearch() {
      this.goPage = 1;
      this.params.page = 1;
      this.initData();
    },
    submit() {
      //确定
      let flag = true;
      if (JSON.stringify(this.multipleSelection) != "{}") {
        //如果未填订单商品数量

        if (!this.multipleSelection.num) {
          this.$message({
            message: "请填写已勾选货位的商品数量",
            type: "warning"
          });
          flag = false;
        }
        if (this.multipleSelection.num > this.multipleSelection.now_goods_num) {
          this.$message({
            message: "数量不得大于货位目前货品数量！",
            type: "warning"
          });
          flag = false;
        }
        if (!flag) return false;
        this.$emit("getAllocationArr", this.multipleSelection);
        this.$parent.goodsAddressVisible = false;
      } else {
        this.$message({
          message: "请选择货位信息",
          type: "warning"
        });
      }
    }
  },
  mounted() {
    this.$set(this, "params", {
      goods_id: this.selectData.goods_id,
      warehouse_id: this.selectData.warehouse_id,
      company_id: this.selectData.company_id
    });
    this.initData();
  }
};
</script>
<style scoped>
.pr20 {
  padding-right: 20px;
}
</style>
